import React from 'react'
import { useNavigate } from 'react-router';
import { useInput } from './misc/Context'

const SearchInput = () => {

  const [input, setInput] = useInput();
  const navigate = useNavigate()

  const OnInputChange = (ev) => {
    setInput(ev.target.value)
  }

  const onSearchClick = () => {
    navigate('/search')
  }

  return (
    <div >
      <input id='search' type='text' name='search' placeholder='search movie/tv-show'
        onChange={OnInputChange}
        className="md:ml-2 p-1 focus:bg-blue-100 text-sm text-gray-700 rounded-full"
        value={input} />

      <button
        className=' text-slate-900 text-sm font-semibold bg-slate-400 px-2 py-1 ml-1 
        rounded-full hover:bg-slate-300'
        onClick={onSearchClick} >
        Search
      </button>
    </div>
  )
}

export default SearchInput