import React from 'react'
import { useSave } from './misc/Save.Context';
import ShowCard from './ShowCard';

const ShowGrid = ({ data, mediaType }) => {

    const { addTv, removeTv, addMovie, removeMovie } = useSave()

    const changeList = (showId, action, media) => {

        if (media === 'tv') {
            if (action === "ADD") {
                addTv(showId)
            }
            if (action === "REMOVE") {
                removeTv(showId)
            }
        }

        if (media === 'movie') {
            if (action === "ADD") {
                addMovie(showId)
            }
            if (action === "REMOVE") {
                removeMovie(showId)
            }
        }

    }

    return (
        <div className="flex flex-wrap justify-center" >
            {
                data.results.map((item) => {
                    return (
                        <ShowCard
                            key={item.id}
                            id={item.id}
                            title={item.title}
                            name={item.name}
                            mediaType={item.media_type ? item.media_type : mediaType}
                            poster={item.poster_path}
                            releaseDate={item.release_date}
                            rating={item.vote_average}
                            changeList={changeList}
                        />
                    )
                })
            }
        </div>
    )
}

export default ShowGrid