import React from 'react'
import { Panel, Rate } from 'rsuite'
// import PlusRoundIcon from '@rsuite/icons/PlusRound';
import { useNavigate } from 'react-router';
import { useSave } from './misc/Save.Context';
import bookmark from '../images/bookmarkWhite.png'

const ShowCard = ({ name, title, poster, id, rating, mediaType, releaseDate, changeList }) => {

    const { tvList, movieList } = useSave()
    const navigate = useNavigate()
    const isShowExist = tvList.includes(id) || movieList.includes(id)

    const addShow = () => {
        changeList(id, "ADD", mediaType)
        // console.log(id, "ADD", mediaType);
    }

    const removeShow = () => {
        changeList(id, "REMOVE", mediaType)
        // console.log(id, "REMOVE", mediaType);
    }

    const onShowClicked = () => {

        if (mediaType === "tv") {
            navigate(`/tv/${id}`)
        }

        if (mediaType === "movie") {
            navigate(`/movie/${id}`)
        }
    }

    return (
        <Panel className='w-48 '>
            <div className='relative' >
                <button className='m-0 p-0' onClick={onShowClicked} >
                    <img src={`https://image.tmdb.org/t/p/original${poster}`}
                        className='object-fill h-56 rounded-lg
                         border-gray-500 border-2 shadow-lg
                          shadow-slate-400 hover:scale-105  
                          transition ease-in-out delay-75 '
                        alt="poster"
                        loading='lazy'
                    />
                </button>
                <div className='absolute top-1 right-2 opacity-70' >
                    {/* <IconButton size='xs' color={isShowExist ? "red" : "blue"} appearance="primary"
                        icon={<PlusRoundIcon />}
                        onClick={isShowExist ? removeShow : addShow}>
                    </IconButton> */}
                    <button onClick={isShowExist ? removeShow : addShow}
                        className='border-1 border-gray-900 rounded-sm hover:scale-110 transition ease-in-out delay-75' >
                        <img src={bookmark} alt="bookmark" className='h-6 rounded-sm m-0 p-0'
                            style={{ background: `${isShowExist ? "#30b337" : "#3492eb"}` }} />
                    </button>

                </div>
                {/* <div className='w-10 absolute bottom-0 left-0 bg-black bg-opacity-90 rounded-full' >
                    <Progress.Circle percent={Math.round(rating) * 10} strokeColor="#ffc107" />
                </div> */}
            </div>

            <div className='flex items-center justify-center ' >
                <Rate defaultValue={1} readOnly max={1} size="xs" />
                <p className='text-gray-500 text-xs ' >
                    <span className='p-1 bg-purple-500 bg-opacity-50 rounded-md' > {`${rating.toFixed(1)}`}</span>
                    {`${' | '}${releaseDate ? releaseDate : 'unknown'}`}
                </p>
            </div>

            <div className='text-center' >
                <button onClick={onShowClicked} >
                    <h6 className='text-gray-700 hover:text-blue-400 ' >
                        {name ? name : title}
                    </h6>
                </button>
            </div>
        </Panel>

    )
}

export default ShowCard