import React from 'react'

const Videos = ({ visible, onClose, videoId }) => {

    const handleClose = (e) => {
        if (e.target.id === 'container') onClose();
    }

    if (!visible) return null;

    return (
        <div
            id='container'
            onClick={handleClose}
            className='fixed inset-0 bg-opacity-80 bg-black flex justify-center items-center backdrop-blur-sm ' >
            <iframe
                className=' w-full max-w-5xl aspect-video rounded-lg'
                src={`https://www.youtube.com/embed/${videoId}?autoplay=1&mute=1`}
                title="YouTube video player"
                frameBorder="0"
                allowFullScreen>
            </iframe>
        </div>
    )
}

export default Videos