import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router'
import { Loader } from 'rsuite';
import MainPageLayout from '../components/MainPageLayout';
import { ApiGetImages, ApiGetMedia, ApiGetVideos } from '../components/misc/config';
import Videos from '../components/Videos';
import playIcon from '../images/play.png'

const Movie = () => {

  const [movie, setMovie] = useState([])
  const [videos, setVideos] = useState([])
  const [images, setImages] = useState([])
  const [visible, setVisible] = useState(false)
  const [videoId, setVideoId] = useState('')
  const [isLoading, setIsLoading] = useState(true)
  const { id } = useParams()

  useEffect(() => {

    ApiGetMedia(`movie/${id}`).then(r => setMovie(r))
    ApiGetVideos(`movie/${id}`).then(r => setVideos(r))
    ApiGetImages(`movie/${id}`).then(r => setImages(r))

    setIsLoading(false)
  }, [id])

  const videoClick = (videoId) => {
    setVideoId(videoId);
    setVisible(p => !p)
  }

  const handleOnClose = () => {
    setVisible(p => !p)
  }


  // const hours = Math.floor(movie.runtime / 60);
  // const minutes = movie.runtime % 60;

  // console.log(videos);
  // console.log(movie);

  return (
    <MainPageLayout>
      {isLoading && !movie && !videos && !images &&
        <div className='h-full flex justify-center items-center' >
          <Loader size='lg' />
        </div>}

      {!isLoading && movie && videos && images &&
        <>
          <section className='h-screen' >
            <div className='h-screen relative ' >
              <img src={`https://image.tmdb.org/t/p/original${movie.backdrop_path}`}
                className='h-2/3 w-full mx-auto  object-cover  rounded-b-lg '
                loading='lazy'
                alt="backdrop" />

              <img src={`https://image.tmdb.org/t/p/original${movie.poster_path}`}
                className="object-fill rounded-xl h-96 shadow-lg
                 shadow-slate-400 absolute bottom-11 left-2 md:left-5 "
                loading='lazy'
                alt="poster" />
            </div>
          </section>

          <section className='w-full flex flex-wrap justify-evenly ' >
            {videos.results?.map((item) => {
              return <div key={item.id} >
                <button
                  onClick={() => {
                    videoClick(item.key)
                  }} >
                  <div
                    style={{
                      backgroundImage:
                        `url("https://image.tmdb.org/t/p/original${images.backdrops &&
                          images.backdrops.length > 0 ?
                          images.backdrops[0].file_path : movie.backdrop_path}")`
                    }}
                    className='w-80 h-48 mb-2 bg-cover rounded-lg ' >
                    <div
                      className='w-full h-full bg-black bg-opacity-30 rounded-lg
                    flex flex-col justify-center items-center text-white' >
                      <img src={playIcon} alt="play" />
                      <h5 className='p-2 bg-black bg-opacity-80 rounded-lg' >{item.type}</h5>
                    </div>
                  </div>
                </button>

              </div>
            })
            }
          </section>

          <Videos visible={visible} onClose={handleOnClose} videoId={videoId} />
        </>
      }
    </MainPageLayout>
  )
}


export default Movie